@tailwind base;
@tailwind components;
@tailwind utilities;

/* === BASE BODY STYLING === */
body {
  margin: 0 150px;
  margin-top: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
  color: #333;
  background-color: #f9f9f9;
}

/* === HEADER === */
.header-container {
  position: sticky;
  top: 0;
  z-index: 1000;
  display: flex;
  height: 40px;
  width: 130%;
  margin-left: -14%;
  align-items: center;
  padding: 10px 20px;
  background: white;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

header {
  position: sticky;
  top: 0;
  z-index: 1000;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 30px;
  border-radius: 50px;
  width: 50%;
  margin: 0 25%;
  border-bottom: 4px solid #eaeaea;
  background: #3d3a38;
  color: white;
}

header .logo {
  font-size: 1rem;
  font-family: 'ControlledFont', sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 0.1rem;
}

header nav a {
  margin-left: 20px;
  text-decoration: none;
  color: white;
  font-size: 0.8rem;
  transition: color 0.3s;
}

header nav a:hover,
.search-btn:hover {
  color: #0070f3;
}

/* === SEARCH & MENU === */
.search-btn {
  background: transparent;
  border: none;
  cursor: pointer;
  font-size: 0.1rem;
  margin-left: 10px;
  color: inherit;
  transition: color 0.3s;
}

.search-container.active .search-input {
  width: 150px;
  padding: 8px 12px;
  opacity: 1;
}

.menu-btn {
  display: flex;
  background: transparent;
  border: none;
  cursor: pointer;
  margin: 0 14px;
  color: white;
  font-size: 1.5em;
}

/* === SIDEBAR === */
.sidebar {
  position: fixed;
  top: 8%;
  left: 0;
  width: 50px;
  height: 100vh;
  background: white;
  color: black;
  padding: 20px;
  overflow: hidden;
  z-index: 999;
  transition: width 0.3s ease-in-out;
}

.sidebar.expanded {
  width: 250px;
}

.sidebar .menu-items a {
  display: flex;
  align-items: center;
  padding: 10px;
  color: #333;
  text-decoration: none;
  font-size: 18px;
  white-space: nowrap;
}

.sidebar .menu-items a i {
  margin-right: 5px;
  min-width: 20px;
}

.sidebar .menu-items a:hover {
  background-color: #e0e0e0;
}

.sidebar:not(.expanded) .menu-items a span {
  display: none;
}

.menu-items {
  margin-top: 40%;
  margin-left: -105%;
  background-color: white;
  width: 9%;
}

.menu-items a .link-text {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  margin-left: 10px;
  font-size: 15px;
}

.menu-items a:hover .link-text {
  opacity: 1;
  visibility: visible;
}

/* === GALLERY & ARTWORK === */
.section h2 {
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 20px;
  text-align: center;
}

.artwork-gallery {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  background-color: white;
}

.artwork-card {
  width: 390px;
  background: white;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
}

.artwork-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.2);
}

.artwork-card img {
  width: 100%;
  height: 280px;
  object-fit: cover;
}

.artwork-info {
  padding: 15px;
}

.artwork-title {
  font-size: 1.5rem;
  font-weight: 600;
  color: black;
  margin-bottom: 8px;
}

.artwork-description {
  font-size: 1rem;
  color: #666;
  margin-bottom: 10px;
}

.artwork-meta {
  display: flex;
  justify-content: space-between;
  font-size: 0.9rem;
  color: #888;
}

/* === BUTTONS === */
.buttons-container {
  display: flex;
  align-items: center;
}

.flower-button {
  background: linear-gradient(135deg, #ff9800, #ff5722);
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: bold;
  padding: 5px;
  font-size: 23px;
  transition: all 0.3s ease;
}

.flower-button:hover {
  background: linear-gradient(135deg, #ff5722, #d84315);
  transform: scale(1.05);
}

.buy-btn {
  display: flex;
  align-items: center;
  gap: 8px;
  background: linear-gradient(5deg, #d94f70, white);
  color: white;
  padding: 10px;
  margin-top: -5px;
  margin-left: 30px;
  font-size: 14px;
  font-weight: 500;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.buy-btn:hover {
  background: linear-gradient(90deg, #ff0040, #ff007f);
  transform: scale(1.05);
}

/* === MODALS === */
.modal,
.rating-modal {
  position: fixed;
  z-index: 2000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-content,
.rating-modal-content {
  background-color: #fff;
  color: #000;
  padding: 20px;
  border-radius: 12px;
  max-width: 1000px;
  width: 90%;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  position: relative;
}

.modal-layout {
  display: flex;
  gap: 20px;
}

.modal-image-container img {
  width: 100%;
  max-height: 400px;
  object-fit: cover;
  border-radius: 8px;
}

.modal-text-content h3 {
  font-size: 1.8rem;
  margin-bottom: 10px;
}

.modal-text-content p {
  font-size: 1rem;
  color: #666;
}

.modal-blockchain {
  padding: 8px 12px;
  background: linear-gradient(to bottom, #0f2027, #203a43, #2c5364);
  color: #fff;
  border-radius: 6px;
  margin-top: 15px;
  box-shadow: 0px 0px 20px rgba(0, 122, 255, 0.5);
}

/* === FOOTER === */
.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: #fff;
  box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.1);
  padding: 10px 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.footer a {
  text-decoration: none;
  color: black;
  font-size: 14px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer a:hover {
  color: #444;
}

.notification-badge {
  background: red;
  color: white;
  border-radius: 50%;
  padding: 2px 6px;
  font-size: 12px;
  margin-left: 5px;
}

/* === ANIMATIONS === */
@keyframes fadeIn {
  from { opacity: 0; transform: scale(0.9); }
  to { opacity: 1; transform: scale(1); }
}

@keyframes fadeInUp {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

/* === RESPONSIVE DESIGN === */
@media (max-width: 1280px) {
  body {
    margin: 0 30px;
  }

  .artwork-gallery {
    grid-template-columns: repeat(2, 1fr);
  }

  header {
    width: 90%;
    margin: 0 auto;
  }

  .artwork-card {
    width: 100%;
  }

  .sidebar.expanded {
    width: 180px;
  }

  .modal-layout {
    flex-direction: column;
  }

  .modal-image-container,
  .modal-text-content {
    max-width: 100%;
  }
}
